.contact-container {
  width: 100vw;
  height: calc(var(--vh, 1vh) * 100);
  background-color: var(--warna-cream);
  color: var(--warna-biru);
}
.contact-container form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: calc(var(--vh, 1vh) * 100);
}
.contact-container form textarea {
  resize: none;
}